* {
  margin: 0;
  padding: 0;
}
body {
  background-color: #458cff;
  margin: 0;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: black;
  line-height: 1.6;
  height: 100%;
}
html {
  scroll-behavior: smooth;
}

.parent {
  margin: 20px;
  height: 75vh;
  width: 98vw;
}
.CenterMe {
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
}

.image {
  height: 200px;
  width: auto;
}

.parentX {
  background-color: black;
  background-image: url(/static/media/falls.281ee6c9.png);
  background-size: cover;
  background-position: center;
  opacity: 5;
  height: 100vh;
  width: 100vw;
}
.child1 {
  display: flex;
  height: 90vh;
  flex-direction: column;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical*/
}
.child2 {
  display: flex;
  height: 10vh;
  padding: 10px;
  margin-bottom: 10px;
  flex-direction: column;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical*/
}
#headline {
  font-family: "Pacifico";
  font-weight: 400;
  font-size: 4vw;
  color: #ffffff;
  line-height: 1em;
}

#subtitle {
  margin-top: 10px;
  font-family: "Pacifico", cursive;
  font-size: 1.5vw;
  color: #ffffff;
  line-height: 1em;
}

.scrolling-wrapper-flexbox {
  margin: 0;
  margin: 10px;

  width: 98vw;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  &::-webkit-scrollbar {
    display: none;
  }
}
.card {
  flex: 0 0 auto;
  margin: 20px;
  max-width: 300px;
  height: auto;
}
.imgAvatar {
  height: 50px;
}

.listX:hover {
  background-color: #d6e3f4; /* "#D6E3F4"*/
  cursor: pointer;
}
.listContainer {
  flex: 1 1;
}

.responsive {
  max-width: 100%;
  height: 80px;
}

